* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: 1px solid aqua;
}

body {
    background-color: #111111;
    height: 100vh;
}

button {
    cursor:pointer;
}

#root {
    margin-top: 100px;
    display: flex;
    align-items: center;
    height: 100%;
    flex-direction: column;
}

footer {
    display: flex;
    align-items: center;
    color: white;
    height: 70px;
}